$spacing--base: 50px;

$spacing--body: 2 * $spacing--base;

$colour--light: #fff;

$colour--dark: #000;

$colour--accent: #fe5f55;

$colour--link: darken($colour--accent, 10%);

$colour--selection: $colour--accent;

$type--base-size: 16pt;

$type--base-line-height: 1.5;

$font--general: "Suisse Intl", "Helvetica Neue", Helvetica,
  "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Osaka, "メイリオ", Meiryo,
  "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;

$easing-general: cubic-bezier(0.445, 0.05, 0.55, 0.95);

$transition-general: all 0.1s $easing-general;

$breakpoint: 768px;
